




















































export default {
	data() {
		return {
			package : [
				'1、套餐一说明的第一条;',
				'2、套餐一说明的第二条;',
				'3、套餐一说明的第三条;',
				'4、套餐一说明的第四条;'
			],
			packageActiveIndex : 1,
			packageActiveTxt : '一'
		};
	},
	methods: {
		chooseApackage(e) {
			let index = e.currentTarget.dataset.index;
			this.packageActiveIndex = index;
			if(index == 1) {
				this.package = [
					'1、套餐一说明的第一条;',
					'2、套餐一说明的第二条;',
					'3、套餐一说明的第三条;',
					'4、套餐一说明的第四条;'
				];
				this.packageActiveTxt = '一';
			} else if(index == 2) {
				this.package = [
					'1、套餐二说明的第一条;',
					'2、套餐二说明的第二条;',
					'3、套餐二说明的第三条;',
					'4、套餐二说明的第四条;'
				];
				this.packageActiveTxt = '二';
			} else if (index == 3) {
				this.package = [
					'1、套餐三说明的第一条;',
					'2、套餐三说明的第二条;',
					'3、套餐三说明的第三条;',
					'4、套餐三说明的第四条;'
				];
				this.packageActiveTxt = '三';
			}
		},
		payMoney() {
			// 目前认为是支付成功
			uni.reLaunch({
				url: '../main_index/main_index?from=waitPay'
			});
		}
	}
}
